<template>
  <!-- 空页面，但模板定义不能删，否则render出错 -->
  <template></template>
</template>
<script>
export default {
  created() {
    const { params, query } = this.$route
    const { path } = params
    this.$router.replace({ path: "/" + path, query })
  },
  render: function (h) {
    return h() // avoid warning message
  },
}
</script>
